import axios from 'axios';
import store from '../../store/index'

export const postController = {

    getItems: async function () {
        var mainUrl = store.state.mainUrl;
        var data = [];
        await axios.get(mainUrl + '/post').then((res) => {
            data = res.data.data
            store.commit("SET_NEWS", data);
        });
        return data;
    },
    addPost: async function (file, post) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        formData.append("image", file);
        formData.append("title[en]", post.title.en);
        formData.append("title[ar]", post.title.ar);
        formData.append("title[tr]", post.title.tr);
        formData.append("description[en]", post.description.en);
        formData.append("description[ar]", post.description.ar);
        formData.append("description[tr]", post.description.tr);
        await axios.request({
            method: "POST",
            url: mainUrl + "/post",
            data: formData,
            headers: this.configAxios(),
        }).then(res => {
            if (res.status == 200) {
                if (res.data.state) {
                    state = true;
                    this.ShowAlert('secondary', 'Don Add Success ')
                    console.log(res.data);
                } else {
                    this.ShowAlert('red', res.data.error)
                }
            }
        }).catch(error => {
            console.log(error.response);
            this.ShowAlert('red', 'An error ' + error)
        });
        return state;
    },
    deleteItem: async function (id) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        await axios
            .request({
                url: mainUrl + "/post/" + id,
                method: "DELETE",
                headers: this.configAxios(),
            }).then(res => {
                console.log(res.data);
                if (res.status == 200) {
                    state = true;
                    this.ShowAlert('secondary', 'done delete success');
                }
            }).catch(error => {
                state = false
                this.ShowAlert('red', 'An Error ERROR CODE' + error);
            }
            );
        return state;
    },
    UpdatePost: async function (file, post) {

        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        formData.append("image", file);
        formData.append("nation_id", post.nation_id);
        formData.append("description[en]", post.description.en);
        formData.append("description[ar]", post.description.ar);
        formData.append("description[tr]", post.description.tr);
        formData.append("title[en]", post.title.en);
        formData.append("title[ar]", post.title.ar);
        formData.append("title[tr]", post.title.tr);
        await axios
            .request({
                method: "POST",
                url: mainUrl + "/post/" + post.id,
                data: formData,
                headers: this.configAxios(),
            }).then(res => {
                if (res.status == 200) {
                    if (res.data.state) {
                        this.ShowAlert('secondary', 'done update success  ');
                        state = true;
                    }
                } else {
                    state = true;
                    this.ShowAlert('red', 'An error  : ' + res.status);
                }
            }).catch(error => {
                console.log(error.response);
                state = false;
                this.ShowAlert('red', 'An Error ðŸ˜¥ please contact Us '
                    + error);
            })

        return state;
    },
    ShowAlert: function (color, message) {
        store.dispatch('showAlert', {
            color: color, message: message
        })
    },
    configAxios() {
        var token = store.state.token;
        return {
            "Content-type": "multipart/form-data",
            "Content-Disposition": "form-data",
            Authorization: "Bearer " + token,
        }
    }
}